@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

body {
  font-family: "Roboto", sans-serif;
}

.app {
  display: flex;
  justify-content: center;
  padding-top: 5rem;
}

.wrap {
  width: 500px;
}

$height: 46px;

.select {
  width: 100%;
  position: relative;
  z-index: 100;

  &.open {
    .select__dropdown {
      display: block;
    }

    .select__input {
      border-bottom: none;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }

    .select__backdrop {
      display: block;
    }
  }

  &__input {
    display: flex;
    align-items: center;
    justify-content: space-between;

    height: $height;

    font-weight: bold;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 0 1rem;
    cursor: pointer;
  }
  &__dropdown {
    display: none;
    position: absolute;
    top: $height;
    left: 0;
    right: 0;

    max-height: 200px;

    border: 1px solid #ccc;
    border-radius: 5px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.3);

    overflow-y: auto;
  }

  &__list {
    padding: 0;
    margin: 0;
    list-style: none;
  }

  &__backdrop {
    position: fixed;
    display: none;
    z-index: -1;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: transparent;
  }

  &__item {
    padding: 1rem;
    border-bottom: 1px solid #ccc;

    &.selected {
      background-color: #ffe1c6;
    }

    &:hover {
      cursor: pointer;
      background-color: #eee;
      transition: 0.15s background-color ease-in;
    }
  }
}
